  height: 20px;
  border: 1px solid var(--var-accent-color);
  background: var(--var-primary-bg-color);
  text-align: center;
  color: var(--var-accent-color);
  cursor: pointer;
  border-radius: 0 5px 5px 0;
  padding-top: 21px;
  font-size: 20px;
}

.wrap {
  width: 30%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.switch {
  width: 60px;
  height: 34px;
  display: inline-block;
  position: relative;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  border: 2px solid var(--var-accent-color);
  background-color: var(--var-primary-bg-color);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 26px;
  width: 26px;
  background-color: var(--var-primary-bg-color);
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 4px;
}

input:checked + .slider {
  background-color: var(--var-accent-color);
  border: solid 2px var(--var-primary-bg-color);
}

input:checked + .theme__light {
  color: var(--var-accent-color);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--var-accent-color);
}

input:checked + .slider:before {
  background-color: var(--var-primary-bg-color);
  transform: translateX(26px);
}

.slider.round {
  background-color: var(--var-primary-bg-color);
  border-radius: 34px;
}

.slider.round:before {
  background-color: var(--var-accent-color);
  border-radius: 50%;
}

.search__form input {
  width: 100%;
  height: 42px;
  border: 2px solid var(--var-accent-color);
  background: var(--var-primary-bg-color);
